export default "import * as React from 'react';\r\nimport Button from '@mui/material/Button';\r\nimport Snackbar from '@mui/material/Snackbar';\r\n\r\nexport default function PositionedSnackbar() {\r\n  const [state, setState] = React.useState({\r\n    open: false,\r\n    vertical: 'top',\r\n    horizontal: 'center',\r\n  });\r\n\r\n  const {vertical, horizontal, open} = state;\r\n\r\n  const handleClick = (newState) => () => {\r\n    setState({open: true, ...newState});\r\n  };\r\n\r\n  const handleClose = () => {\r\n    setState({...state, open: false});\r\n  };\r\n\r\n  const buttons = (\r\n    <React.Fragment>\r\n      <Button\r\n        onClick={handleClick({\r\n          vertical: 'top',\r\n          horizontal: 'center',\r\n        })}\r\n      >\r\n        Top-Center\r\n      </Button>\r\n      <Button\r\n        onClick={handleClick({\r\n          vertical: 'top',\r\n          horizontal: 'right',\r\n        })}\r\n      >\r\n        Top-Right\r\n      </Button>\r\n      <Button\r\n        onClick={handleClick({\r\n          vertical: 'bottom',\r\n          horizontal: 'right',\r\n        })}\r\n      >\r\n        Bottom-Right\r\n      </Button>\r\n      <Button\r\n        onClick={handleClick({\r\n          vertical: 'bottom',\r\n          horizontal: 'center',\r\n        })}\r\n      >\r\n        Bottom-Center\r\n      </Button>\r\n      <Button\r\n        onClick={handleClick({\r\n          vertical: 'bottom',\r\n          horizontal: 'left',\r\n        })}\r\n      >\r\n        Bottom-Left\r\n      </Button>\r\n      <Button\r\n        onClick={handleClick({\r\n          vertical: 'top',\r\n          horizontal: 'left',\r\n        })}\r\n      >\r\n        Top-Left\r\n      </Button>\r\n    </React.Fragment>\r\n  );\r\n\r\n  return (\r\n    <div>\r\n      {buttons}\r\n      <Snackbar\r\n        anchorOrigin={{vertical, horizontal}}\r\n        open={open}\r\n        onClose={handleClose}\r\n        message='I love snacks'\r\n        key={vertical + horizontal}\r\n      />\r\n    </div>\r\n  );\r\n}\r\n";